button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: #9597ad;
  font-family: 'Josefin Sans';
  font-size: 1.125rem;
}

.todolist {
  border-radius: 1rem;
  background-color: hsl(235, 24%, 19%);
  width: 100%;
}

.todolist__completed-section {
  display: flex;
  justify-content: space-between;
}
