p {
  color: #9597ad;
}

.todo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid hsl(234, 11%, 52%);
  width: 100%;
}

.todo__left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.todo__close {
  cursor: pointer;
  content: url('../images/icon-cross.svg');
}

.todo__icon {
  cursor: pointer;
  border-radius: 50%;
  padding: 0.6rem;
}

.todo__completed {
  content: url('../images/icon-check.svg');
  background: linear-gradient(
    to right,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
}

.todo__completed_paragraph {
  text-decoration: line-through;
  color: #46485e;
}

.todo__incomplete {
  border: 1px solid #fff;
  width: 2rem;
  height: 2rem;
  /* background-color: red; */
}
